angular.module('app').run(['$templateCache', function($templateCache) {$templateCache.put('app/hello.html','<div ng-if="!$ctrl.data">\n    <p class="centre-text">{{ $ctrl.hello }}</p>\n    <!-- Trigger/Open The Modal -->\n    <button id="myBtn" class="artist-button" ng-click="$ctrl.openModal()">Search Artist</button>\n\n    <!-- The Modal -->\n    <div id="myModal" class="modal" ng-if="$ctrl.show">\n\n        <!-- Modal content -->\n        <div class="modal-content">\n            <div class="header">\n                Enter Search Criteria\n                <span class="close" ng-click="$ctrl.hideModal()">&times;</span>\n            </div>\n            <form name="form" ng-submit="$ctrl.getData()" class="css-form" novalidate>\n                <div>\n                    <label>\n                        Artist Name:<input type="text" ng-model="$ctrl.name" name="name" class="user" username></label><br>\n                    <span ng-show="form.name.$pending.username">Checking if this name is available...</span>\n                    <span ng-show="form.name.$error.username">Please enter valid name </span>\n                </div>\n\n                <div>\n                    <label>No. of Tracks:\n                        <input type="number" ng-model="$ctrl.limit" name="size" min="0" max="4" integer></label><br>\n                    <span ng-show="form.size.$error.integer">The value is not a valid integer!</span>\n                    <span ng-show="form.size.$error.min || form.size.$error.max"> The value must be in range 0 to 4!</span>\n                </div>\n\n                <input type="submit" id="submit" value="Search" class="search" ng-disabled="form.$invalid">\n            </form>\n        </div>\n    </div>\n</div>\n<div ng-if="$ctrl.data" class="result">\n    <div class="header">\n        <h1 class="heading">Search result for "{{$ctrl.searched}}" is here.</h1>\n\n        <span class="clear" ng-click="$ctrl.clearData()">(Clear)</span>\n    </div>\n    <div class="content" ng-repeat="artist in $ctrl.data.results">\n        <div class="image"><img src="{{artist.artworkUrl60}}" alt=""></div>\n        <div class="description">\n            <label>Artist Name:</label><span>{{artist.artistName}}</span><br>\n            <label>Track Name:</label><span> {{artist.trackName}}</span><br>\n            <div class="long">\n                <p>{{artist.longDescription}}</p>\n            </div>\n        </div>\n    </div>\n</div>\n');}]);